@import "@/assets/styles/_variables.scss";

    .chars {
        @include content;

        &__list {
            padding-top: 12px;
        }

        &__item {
            @include css_anim;

            display: flex;
            align-items: center;
            justify-content: flex-start;
            width: 100%;
            color: $black;
            padding: 8px 12px;
            border-radius: 4px;
            text-decoration: none;
            text-align: left;
            cursor: pointer;
            box-shadow: 0 0 10px 0 transparentize($black, .85);

            &:nth-child(n+2) {
                margin-top: 8px;
            }

            &:hover {
                @include css_anim;

                background-color: lighten($gray, 14%);
            }

            &-img {
                content: '';
                display: block;
                width: 48px;
                height: 48px;
                background-repeat: no-repeat;
                background-position: center center;
                background-size: cover;
                flex-shrink: 0;
            }

            &-cont {
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                padding-left: 12px;
            }

            &-name {
                display: block;
                overflow: hidden;
                text-overflow: ellipsis;
                width: 100%;
            }

            &-info {
                display: flex;
                align-items: flex-start;
                flex-wrap: wrap;
                margin-top: 8px;
                border-top: 1px solid $gray;
                width: 100%;
                padding-top: 4px;
            }

            &-lvl, &-class {
                max-width: 100%;
            }

            &-class {
                margin-left: 8px;
            }

            &-lvl {
                font-weight: 600;
            }
        }
    }
